@import "../../configs/variables";
#Card:hover {
  box-shadow: 0 10px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);

}
#Card {

  background-color: white;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  border-radius: 10px;
  box-sizing: border-box;
  transition: box-shadow .5s;

  .cardHeader {
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: $primary-color;
  }

  #title {
    color: white;
    text-align: center;
    margin-bottom: 0;
    font-weight: 100;
  }

  #description {
    padding: 16px;
    color: $primary-dark;
    text-align: left;
    letter-spacing: 1px;
    line-height: 1.7;
  }

  .cardIcon {
    background-color: white;
    padding: 15px;
    border-radius: 70px;
    height: 50px;
    width: 70px;
  }

}