@import "../../configs/variables";

#About {

  margin: 0 $side-space;

  .header {
    color: $primary-color;
    margin-top: 60px;
    text-align: center;
  }

  .text {
    color: $primary-dark;
    text-align: justify;
    opacity: .87;
    letter-spacing: 1px;
    line-height: 1.7;
  }

  .cardContainer {
    width: 30%;
    max-width: 300px;
  }

  .cardsContainer {
    align-items: flex-start;
    margin-top: 64px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

  }

  @media (max-width: 600px) {
    margin: 0 $side-space-600;

    .text {
      text-align: left;
    }

    .cardsContainer {
      flex-direction: column;
    }

    .cardContainer {
      width: 100%;
      max-width: unset;
    }

  }

}