@import "./../../configs/variables";

#HeaderComponent {

  box-sizing: border-box;
  padding: 100px $side-space 0;
  background-color: $primary-color;
  height: 500px;

  .headerTitle {
    font-size: 3em;
    line-height: 1em;
    color: white;
    font-weight: 300;
    margin: 32px 0 0;
  }

  .headerSubheader {
    color: white;
    max-width: 400px;
    font-weight: 400;
    line-height: 30px;
  }

  .image1 {
    background-image: url("../../assets/images/image1.jpg");
    height: 350px;
    width: 250px;
    min-width: 250px;
    background-position: center;
    background-size: cover;
    border-radius: 15px;
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border: solid $neutral-color 2px;
    position: relative;
    transform: rotate(15deg);
    right: -400px;
  }

  .image2 {
    background-image: url("../../assets/images/image2.png");
    height: 350px;
    width: 250px;
    min-width: 250px;
    background-position: center;
    background-size: cover;
    border-radius: 15px;
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border: solid $neutral-color 2px;
    position: relative;
    left: -30px;
    transform: rotate(-15deg);
  }

  .image3 {
    background-image: url("../../assets/images/image3.jpg");
    height: 220px;
    width: 308px;
    min-width: 350px;
    background-position: center;
    background-size: cover;
    border-radius: 15px;
    box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border: solid $neutral-color 2px;
    position: relative;
    left: -190px;
    top: 100px;
  }

  #leftSide {
    width: 50%;
  }

  #rightSide {
    width: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .headerContent {
    display: flex;
    flex-direction: row;
    margin-top: 46px;
  }

  .contactUsButton:hover {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    background-color: #d5d5d5;
    transition: background-color .3s;
  }

  .contactUsButton:active {
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
    background-color: #b6b6b6;
    transition: background-color .1s;
  }

  .contactUsButton {
    background: #fff;
    color: $secondary-color;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    border-radius: 50px;
    padding: 15px 45px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    line-height: 1.5;
    outline: none;
    cursor: pointer;
    border: none;
  }

  @media (max-width: 1100px) {

    .image1 {
      height: 300px;
      min-width: 200px;
      right: -362px;
      z-index: 1;
      transform: rotate(20deg);
    }

    .image2 {
      height: 300px;
      min-width: 200px;
      left: 40px;
      transform: rotate(-20deg);
    }

    .image3 {
      height: 193px;
      width: 294px;
      min-width: 325px;
      left: -168px;
      top: 122px;
      z-index: 4;
    }

  }

  @media (max-width: 750px) {

    .image1 {
      height: 250px;
      min-width: 150px;
      right: -280px;
      z-index: 1;
      transform: rotate(20deg);
    }

    .image2 {
      height: 250px;
      min-width: 150px;
      left: 40px;
      transform: rotate(-20deg);
    }

    .image3 {
      height: 150px;
      min-width: 280px;
      left: -136px;
      top: 90px;
      z-index: 4;
    }

  }

  @media (max-width: 600px) {


    box-sizing: border-box;
    padding: 50px $side-space-600 0;
    height: 100vh;


    .headerContent {
      flex-direction: column;
    }

    .headerTitle {
      font-size: 2em;
      line-height: 1em;
      margin: 0;
    }

    #leftSide {
      width: 100%;
    }

    #rightSide {
      width: 100%;
      margin-top: 40px;
    }

    .image1 {
      height: 300px;
      min-width: 220px;
      right: -355px;
      z-index: 1;
      transform: rotate(20deg);
    }

    .image2 {
      height: 300px;
      min-width: 220px;
      left: -11px;
      transform: rotate(-20deg);
    }

    .image3 {
      height: 217px;
      min-width: 365px;
      left: -236px;
      top: 135px;
      z-index: 4;
    }

  }

  @media (max-width: 460px) {

    .image1 {
      height: 250px;
      min-width: 150px;
      right: -280px;
      z-index: 1;
      transform: rotate(20deg);
    }

    .image2 {
      height: 250px;
      min-width: 150px;
      left: 10px;
      transform: rotate(-20deg);
    }

    .image3 {
      height: 150px;
      min-width: 280px;
      left: -150px;
      top: 90px;
      z-index: 4;
    }


  }

}