@import "../../configs/variables";
#EnterEmail {
  margin: 100px $side-space 50px;
  border-bottom: solid 1px rgba(0, 0, 0, 0.12);
  padding-bottom: 32px;

  .subscribeText {
    font-size: 16px;
    font-weight: 100;
    text-align: left;
    margin: 0;
    color: $secondary-color
  }

  .content {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 auto;
  }

  .inputContainer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  #input:focus {
    border-color: $primary-color;
  }
  #input {
    box-sizing: border-box;
    display: block;
    flex: 1;
    margin-right: 16px;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 2;
    color: rgba(0, 0, 0, 0.87);
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.14);
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    outline: none;
    caret-color: $secondary-color;
  }

  .emailButton:hover {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    background-color: #d5d5d5;
    transition: background-color .3s;
  }

  .emailButton:active {
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
    background-color: #b6b6b6;
    transition: background-color .1s;
  }

  .emailButton {
    background: #fff;
    color: $secondary-color;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    border-radius: 50px;
    padding: 15px 45px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    line-height: 1.5;
    outline: none;
    cursor: pointer;
    border: none;
    transition: background-color .3s;

  }

  .emailButton[disabled] {
    background-color: #d5d5d5 !important;
    color: rgba(0, 0, 0, 0.38);
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12) !important;
    cursor: default;
  }

  @media (max-width: 600px){

    margin: 60px $side-space-600 50px;

    .inputContainer{
      flex-direction: column;
    }

    #input {
      width: 100%;
      margin: 16px 0;
    }

    .subscribeText {
      text-align: center;
      margin: 0;
    }

  }



  }